/* Los estilos específicos para el componente ResetPassword */
.ResetPasswordPage {
    background: var(--bgColor);
    color: var(--whiteColor);
    height: 100vh;
    width: 100%;
    margin: auto;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 7px 50px rgba(0, 0, 0, 0.1);
  
    .container {
      height: 70vh;
      width: 80%;
      max-width: 1000px;
      margin: auto;
      justify-content: space-between;
      border-radius: 10px;
      background: hsl(0, 0%, 91%);
      overflow: auto;
  
      .imgDiv {
        position: relative;
        height: 100%;
        width: 50%;
        background: hsl(0, 0%, 91%);
        display: flex;
        flex-basis: 50%;
        justify-content: center;
        padding: 0 1.5rem;
        text-align: center;
        flex-direction: column;
        margin: auto;
        border-radius: 10px;
        overflow: hidden;
  
        img, .footerDiv {
          position: absolute;
        }
  
        img {
          height: 100%;
          width: 100%;
          object-fit: cover;
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
        }
  
        .textDiv {
          position: relative;
          
          .title {
            color: var(--accent-color);
            font-size: 28px;
            font-weight: 800;
            align-items: flex-end;
          }
  
          p {
            color: var(--whiteColor);
            padding: 1rem;
            font-weight: 400;
          }
        }
  
        .footerDiv {
          bottom: 10px;
          padding: 0 1rem;
          height: 60px;
          background: rgba(44, 62, 80, 0.55);
          left: 0;
          right: 0;
          justify-content: space-between;
          backdrop-filter: blur(1px);
        }
  
        .text {
          color: var(--whiteColor);
        }
  
        .btn {
          background: var(--buttonColor);
          border: none;
          outline: none;
          cursor: pointer;
          border-radius: 8px;
          transition: 0.3s ease;
          padding: 0.8rem 1rem;
          font-weight: 600;
          color: var(--whiteColor);
  
          &:hover {
            background: var(--hover-color);
            color: var(--blackColor);
          }
        }
      }
  
      .formDiv {
        flex-basis: 50%;
        margin: auto;
        flex-direction: column;
        gap: 1.5rem;
        transform: translateY(1rem);
  
        .headerDiv {
          width: 60%;
          padding-bottom: 0.5rem;
  
          img {
            width: 100px;
            padding-bottom: 0.5rem;
          }
  
          h3 {
            font-size: 20px;
            color: var(--blackColor);
            font-weight: 600;
          }
        }
      }
  
      .form {
        gap: 1rem;
  
        .inputDiv {
          .icon {
            color: black;
          }
  
          input {
            background: var(--inputColor);
            color: var(--blackColor);
            outline: none;
            border: none;
            width: 250px;
          }
  
          label {
            color: var(--blackColor);
            font-weight: 400;
            font-size: 14px;
            padding: 0.5rem 0;
            display: block;
          }
  
          .input {
            gap: 0.5rem;
            padding: 0.8rem;
            background: var(--inputColor);
            color: var(--blackColor);
            border-radius: 10px;
          }
        }
  
        .btn {
          padding: 0.8rem;
          background: var(--buttonColor);
          border-radius: 10px;
          color: var(--whiteColor);
          align-items: center;
          margin: auto;
          justify-content: center;
          font-size: 18px;
          width: 100%;
          transition: 0.3s ease;
  
          .icon {
            font-size: 25px;
            transition: 0.3s ease;
          }
  
          &:hover {
            background: var(--hover-color);
            color: var(--blackColor);
  
            .icon {
              color: var(--blackColor);
              transform: translateX(5px);
            }
          }
        }
      }
    }
  }
  
  /* Media queries para responsividad */
  @media (max-width: 768px) {
    .ResetPasswordPage .container {
      width: 90%;
      flex-direction: column;
    }
    
    .ResetPasswordPage .container .imgDiv {
      width: 100%;
      height: 200px;
    }
    
    .ResetPasswordPage .container .formDiv {
      width: 100%;
    }
  }
  
  /* Estilos específicos para pantallas muy pequeñas */
  @media (max-width: 400px) {
    .ResetPasswordPage .container .headerDiv h3 {
      font-size: 18px;
    }
    
    .ResetPasswordPage .container .form .btn {
      font-size: 16px;
    }
    
    .ResetPasswordPage .container .form .inputDiv .input {
      padding: 0.6rem;
    }
  }